// frontend/src/components/LegalPage.tsx
import React, { useState } from 'react';
import { FileCheck, Lock, Cookie, Download, ChevronDown, ChevronUp } from 'lucide-react';

const LegalPage: React.FC = () => {
  const [activeSection, setActiveSection] = useState<'terms' | 'privacy' | 'cookies'>('terms');
  const [expandedSections, setExpandedSections] = useState<string[]>(['1']);

  const toggleSection = (id: string) => {
    setExpandedSections(prev =>
      prev.includes(id) ? prev.filter(s => s !== id) : [...prev, id]
    );
  };

  const sections = {
    terms: {
      icon: FileCheck,
      title: 'תנאי שימוש',
      color: 'blue',
      content: [
        {
          id: '1',
          title: '1. כללי',
          content: `שירות יתרו ("השירות") מסופק על ידי יתרו בע"מ ("החברה"). השימוש בשירות כפוף לתנאים אלה.`
        },
        {
          id: '2',
          title: '2. רישיון שימוש',
          content: `אנו מעניקים לך רישיון מוגבל, לא בלעדי, בלתי ניתן להעברה לשימוש בשירות למטרות עסקיות או אישיות חוקיות.`
        },
        {
          id: '3',
          title: '3. זכויות קניין רוחני',
          content: `כל זכויות הקניין הרוחני בשירות, כולל תוכנה, עיצוב, תוכן וסימנים מסחריים, שייכות לחברה או למעניקי הרישיון שלה.`
        },
        {
          id: '4',
          title: '4. תשלום והחזרים',
          content: `התשלום עבור השירות יתבצע על פי התוכנית שנבחרה. החזרים יתבצעו בהתאם למדיניות ההחזרים המפורטת באתר.`
        },
        {
          id: '5',
          title: '5. הגבלת אחריות',
          content: `השירות מסופק "כמות שהוא". החברה לא תהיה אחראית לכל נזק עקיף, מקרי או תוצאתי הנובע משימוש או אי-שימוש בשירות.`
        },
        {
          id: '6',
          title: '6. סיום השירות',
          content: `אנו שומרים את הזכות להשעות או לסיים את גישתך לשירות בכל עת, עם או ללא הודעה מראש, בגין הפרה של תנאים אלה.`
        }
      ]
    },
    privacy: {
      icon: Lock,
      title: 'מדיניות פרטיות',
      color: 'purple',
      content: [
        {
          id: '1',
          title: '1. איסוף מידע',
          content: `אנו אוספים מידע שאתה מספק ישירות (שם, אימייל, פרטי תשלום) ומידע טכני (IP, דפדפן, התנהגות באתר).`
        },
        {
          id: '2',
          title: '2. שימוש במידע',
          content: `אנו משתמשים במידע כדי לספק ולשפר את השירות, לתקשר איתך, לעבד תשלומים ולמנוע הונאות.`
        },
        {
          id: '3',
          title: '3. הצפנה ואבטחה',
          content: `אנו משתמשים בהצפנה ברמה גבוהה (SSL/TLS) כדי להגן על המידע שלך. כל הנתונים מאוחסנים בשרתים מאובטחים.`
        },
        {
          id: '4',
          title: '4. שיתוף עם צדדים שלישיים',
          content: `אנו לא נשתף את המידע האישי שלך עם צדדים שלישיים ללא הסכמתך, למעט כנדרש על פי חוק או לצורך אספקת השירות.`
        },
        {
          id: '5',
          title: '5. זכויות המשתמש (GDPR)',
          content: `יש לך זכות לגשת, לתקן, למחוק או להגביל את השימוש במידע האישי שלך. צור איתנו קשר לממש זכויות אלה.`
        },
        {
          id: '6',
          title: '6. שמירת נתונים',
          content: `אנו שומרים את המידע שלך כל עוד חשבונך פעיל או כנדרש לצורכי חוק. ניתן לבקש מחיקה בכל עת.`
        }
      ]
    },
    cookies: {
      icon: Cookie,
      title: 'מדיניות Cookies',
      color: 'orange',
      content: [
        {
          id: '1',
          title: '1. מה הם Cookies?',
          content: `Cookies הם קבצי טקסט קטנים המאוחסנים במכשיר שלך כדי לשפר את חווית השימוש באתר.`
        },
        {
          id: '2',
          title: '2. סוגי Cookies שבהם אנו משתמשים',
          content: `- Cookies הכרחיים: נדרשים לתפעול האתר
- Cookies פונקציונליים: שומרים העדפות
- Cookies אנליטיים: עוזרים לנו להבין שימוש באתר
- Cookies שיווקיים: מציגים תוכן רלוונטי`
        },
        {
          id: '3',
          title: '3. ניהול Cookies',
          content: `אתה יכול לנהל או למחוק Cookies דרך הגדרות הדפדפן שלך. שים לב שחסימת Cookies עלולה להשפיע על פונקציונליות האתר.`
        },
        {
          id: '4',
          title: '4. Cookies של צדדים שלישיים',
          content: `אנו משתמשים בשירותים של Google Analytics ושירותים דומים שמציבים Cookies משלהם. אתה יכול לבטל את ההשתתפות דרך הגדרות הפרטיות שלהם.`
        }
      ]
    }
  };

  const currentSection = sections[activeSection];
  const Icon = currentSection.icon;

  return (
    <div className="p-8">
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-white mb-2">משפטי</h1>
        <p className="text-blue-200">תנאים, פרטיות ומדיניות</p>
      </div>

      {/* Tabs */}
      <div className="flex gap-4 mb-8">
        {Object.entries(sections).map(([key, section]) => {
          const SectionIcon = section.icon;
          const isActive = activeSection === key;
          
          return (
            <button
              key={key}
              onClick={() => setActiveSection(key as any)}
              className={`flex items-center gap-3 px-6 py-3 rounded-xl font-semibold transition-all ${
                isActive
                  ? `bg-${section.color}-600 text-white shadow-lg`
                  : 'bg-white/10 text-blue-200 hover:bg-white/20'
              }`}
            >
              <SectionIcon size={20} />
              {section.title}
            </button>
          );
        })}
      </div>

      {/* Content */}
      <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-8 border border-white/20">
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-3">
            <Icon className={`text-${currentSection.color}-400`} size={32} />
            <h2 className="text-2xl font-bold text-white">{currentSection.title}</h2>
          </div>
          
          <button className="flex items-center gap-2 px-4 py-2 bg-white/10 hover:bg-white/20 rounded-lg text-white transition-colors">
            <Download size={20} />
            הורד PDF
          </button>
        </div>

        <div className="space-y-4">
          {currentSection.content.map((item) => {
            const isExpanded = expandedSections.includes(item.id);
            
            return (
              <div
                key={item.id}
                className="bg-white/5 rounded-xl border border-white/10 overflow-hidden"
              >
                <button
                  onClick={() => toggleSection(item.id)}
                  className="w-full flex items-center justify-between p-4 hover:bg-white/5 transition-colors"
                >
                  <h3 className="text-white font-semibold text-right">{item.title}</h3>
                  {isExpanded ? (
                    <ChevronUp className="text-blue-300" size={20} />
                  ) : (
                    <ChevronDown className="text-blue-300" size={20} />
                  )}
                </button>
                
                {isExpanded && (
                  <div className="px-4 pb-4">
                    <p className="text-blue-200 whitespace-pre-line leading-relaxed">
                      {item.content}
                    </p>
                  </div>
                )}
              </div>
            );
          })}
        </div>

        {/* Footer */}
        <div className="mt-8 pt-8 border-t border-white/20">
          <p className="text-blue-300 text-sm text-center">
            עדכון אחרון: 15 אוקטובר 2024 • לשאלות נוספות:{' '}
            <a href="mailto:legal@yitro.ai" className="text-blue-400 hover:text-blue-300">
              legal@yitro.ai
            </a>
          </p>
        </div>
      </div>
    </div>
  );
};

export default LegalPage;
